@import url(https://fonts.googleapis.com/css2?family=El+Messiri&display=swap);
@import url(https://fonts.googleapis.com/css2?family=El+Messiri&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    scroll-behavior: smooth;
}

#landingPageLarge {
    width: 100%;
    height: 100vh;
}

#section1 {
    height: 100%;
    width: 100%;
    background-image: url(/static/media/fingerstouchinglarge.77ddb7ef.jpg);
    background-size: cover;
    font-family: 'El Messiri', sans-serif;
}

.section1Text {
    left: 3%;
    font-size: 60px;
    font-weight: bold;
    font-style: italic;
    margin-left: 2%;
}

#section2 {
    height: 100%;
    width: 100%;
    position: relative;
    background-color: rgb(98, 180, 160);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 25px;
}

#aboutText {
    width: 30%;
    height: 40%;
    position: relative;
    font-family: 'El Messiri', sans-serif;
    text-align: center;
}

#aboutTextHello {
    font-size: 40px;
    margin: 0 auto;
}

#aboutTextText {
    text-align: left;
}

#portIcon {
    width: 20%;
}

#section3 {
    height: 100%;
    width: 100%;
    background-color: rgb(177, 110, 110);
    font-family: 'El Messiri', sans-serif;
    color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.projectLists {
    height: 60%;
    width: 20%;
    font-size: 30px;
}

.projectLists a {
    color: white;
}
#LandingPageSmall {
    font-family: 'El Messiri', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#sectionOne {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#sectionOne p {
    width: 80%;
    font-size: 20px;
    font-weight: bold;
}

#sectionTwo {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(98, 180, 160);
    color: white;
}

#imageIcon {
    width: 60%;
}

#bioTextSmall {
    width: 80%;
    font-size: 20px;
}

#sectionThree {
    width: 100%;
    height: 100vh;
    background-color: rgb(177, 110, 110);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}

#projectListsSmall a {
    color: white;
}
